import React, { useState } from "react";
import axios from "axios";

function RhymeApi() {
  const [rhyme, setRhyme] = useState();
  const [result, setResult] = useState([]);
  const random = Math.floor(Math.random() * result.length);
  console.log(random, result[random]);

  function handleChange(event) {
    const rhyme = event.target.value;
    setRhyme(rhyme);
  }

  function handleSubmit(event) {
    event.preventDefault();

    var options = {
      method: "GET",
      url: "https://wordsapiv1.p.rapidapi.com/words/" + rhyme + "/rhymes",
      headers: {
        "x-rapidapi-key": "d257bc4125msh092415e720b73f8p10e3a0jsn9d1dde59496a",
        "x-rapidapi-host": "wordsapiv1.p.rapidapi.com",
      },
    };

    axios
      .request(options)
      .then(function (response) {
        console.log(response.data);
        setResult(response.data.rhymes.all);
      })
      .catch(function (error) {
        console.error(error);
      });
  }

  return (
    <div className="rhymeContainer">
      <h2>Rhyme</h2>

      <button className="btn-danger" type="submit">
        Search
      </button>

      <form onSubmit={handleSubmit}>
        <div className="form-group">
          <input
            type="text"
            onChange={handleChange}
            className="input-control"
            placeholder="Search for rhyme"
            autoComplete="off"
          />
        </div>
      </form>
      <div className="rhymeValue">
        {result.slice(0, 5).map((rhyme) => (
          <div className="rhyme">
            <h3>{rhyme}</h3>
          </div>
        ))}
      </div>
    </div>
  );
}
export default RhymeApi;
