import React, { useState } from "react";
import axios from "axios";

function DefinitionApi() {
  const [definition, setDefinition] = useState();
  const [result, setResult] = useState([]);
  const [resultX, setResultX] = useState([]);

  function handleChange(event) {
    const definition = event.target.value;
    setDefinition(definition);
  }

  function handleSubmit(event) {
    event.preventDefault();

    var options = {
      method: "GET",
      url:
        "https://wordsapiv1.p.rapidapi.com/words/" +
        definition +
        "/definitions",
      headers: {
        "x-rapidapi-key": "d257bc4125msh092415e720b73f8p10e3a0jsn9d1dde59496a",
        "x-rapidapi-host": "wordsapiv1.p.rapidapi.com",
      },
    };

    axios
      .request(options)
      .then(function (response) {
        console.log(response.data);
        setResult(response.data.definitions[0].definition);
        setResultX(response.data.definitions[1].definition);
        console.log(result);
      })
      .catch(function (error) {
        console.error(error);
      });
  }

  return (
    <div className="definitionContainer">
      <h2>Definition</h2>

      <button className="btn-danger" type="submit">
        Search
      </button>

      <form onSubmit={handleSubmit}>
        <div className="form-group">
          <input
            type="text"
            onChange={handleChange}
            className="input-control"
            placeholder="Search for definition"
            autoComplete="off"
          />
        </div>
      </form>
      <div className="definitionValue">
        <div className="definition">
          <ul>
            <div>{result}</div>
            <div> {resultX} </div>
          </ul>
        </div>
      </div>
    </div>
  );
}
export default DefinitionApi;
