import React, { useState } from "react";
import axios from "axios";

function SynonymApi() {
  const [synonym, setSynonym] = useState();
  const [result, setResult] = useState([]);

  function handleChange(event) {
    const synonym = event.target.value;
    setSynonym(synonym);
  }

  function handleSubmit(event) {
    event.preventDefault();

    var options = {
      method: "GET",
      url: "https://wordsapiv1.p.rapidapi.com/words/" + synonym + "/synonyms",
      headers: {
        "x-rapidapi-key": "d257bc4125msh092415e720b73f8p10e3a0jsn9d1dde59496a",
        "x-rapidapi-host": "wordsapiv1.p.rapidapi.com",
      },
    };

    axios
      .request(options)
      .then(function (response) {
        console.log(response.data);
        setResult(response.data.synonyms);
        console.log(result);
      })
      .catch(function (error) {
        console.error(error);
      });
  }

  return (
    <div className="synonymContainer">
      <h2>Synonym</h2>

      <button className="btn-danger" type="submit">
        Search
      </button>

      <form onSubmit={handleSubmit}>
        <div className="form-group">
          <input
            type="text"
            onChange={handleChange}
            className="input-control"
            placeholder="Search for synonym"
            autoComplete="off"
          />
        </div>
      </form>

      <div className="synonymValue">
        {result.slice(0, 5).map((synonym) => (
          <div className="synonym">
            <h3>{synonym}</h3>
          </div>
        ))}
      </div>
    </div>
  );
}
export default SynonymApi;
